<template>
	<div>
		<div class="contacts--list__item">
			<label class="check">
				<input type="checkbox">
				<div class="check-mark"></div>
			</label>
			<div class="contacts__info"
				:key="job.id">
				<div class="info__primary">
					<p class="info__name">{{ job.title }}</p>
					<p class="info__email">{{ job.type }}</p>
				</div>
			</div>
			<p>
				<span :class="isActive">
                    {{ job.active === 1 ? 'Active' : 'Suspended'}}
				</span>
			</p>
			<p>{{ job.location }}</p>
			<p class="info__address">{{ job.closing_date | date }}</p>
			<p>
				<router-link
					:to="{ name: 'job-detail',
					params: { jobData: jobData, id: job.id }}"
					class="btn btn__primary">
					View Job
				</router-link>
			</p>
		</div>
	</div>
</template>

<script>
export default {
	name: 'JobItem',
	props: {
		jobData: {
			type: Object,
			default() {
				return {
					id: null,
					fname: '',
					lname: '',
					email: '',
					phone: ''
				};
			}
		}
	},
	data() {
		return {
			job: this.jobData
		};
	},
	filters: {
		date(value) {
			return moment(value).format('Do, MMM YYYY');
		}
	},
	computed: {
		isActive() {
			return [
				this.job.active === 1
					? 'badge badge-pill badge-success'
					: 'badge badge-pill badge-danger'
			];
		}
	}
};
</script>

<style scoped>
	.badge {
		font-weight: normal;
	}
	.contacts--list__item {
		display: grid;
		grid-template-columns: 35px 2fr 1fr 1fr 1fr 150px;
	}
</style>
