<template>
	<div>
		<template v-if="jobsLoading">
			<div class="div__loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<h3 class="main__title">Jobs</h3>
				</div>
				<div class="d-flex">
					<button class="btn btn__primary" type="button" name="button"
						data-toggle="modal" data-target="#userModal">
						<span class="btn__icon">
							<svg width="24" height="13" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 5H5V2H3v3H0v2h3v3h2V7h3V5zm10 1c1.66 0 2.99-1.34 2.99-3S19.66 0 18 0c-.32 0-.63.05-.91.14.57.81.9 1.79.9 2.86 0 1.07-.34 2.04-.9 2.86.28.09.59.14.91.14zm-5 0c1.66 0 2.99-1.34 2.99-3S14.66 0 13 0c-1.66 0-3 1.34-3 3s1.34 3 3 3zm6.62 2.16C20.45 8.89 21 9.82 21 11v2h3v-2c0-1.54-2.37-2.49-4.38-2.84zM13 8c-2 0-6 1-6 3v2h12v-2c0-2-4-3-6-3z" fill="#fff"/></svg>
						</span>Add Job</button>
				</div>
			</div>
			<section class="main-grid__row contacts contacts--list">
				<div class="contacts--list__wrapper">
					<div class="contacts--list__item head">
						<div></div>
						<p>Title & Type</p>
						<p>Status</p>
						<p>Location</p>
						<p>Closing Date</p>
						<p>Actions</p>
					</div>
					<div class="contacts__body">
						<template v-if="jobsLoaded && jobs.length">
							<JobItem
								v-for="job in jobs"
								:key="job.id"
								:job-data="job">
								&nbsp;
							</JobItem>
						</template>
						<template v-else-if="jobsLoaded">
							<p>No job found</p>
						</template>
						<template v-else>
							<p>An error occurred while loading jobs, please try again.</p>
						</template>
					</div>
				</div>
			</section>
		</template>
        <Pagination
			:options="{ limit, offset, total, baseUrl }"
			v-if="total > limit"
		/>
        <AddJob />
	</div>
</template>

<script>
import { getJobs, getEmployerJobs } from '../../utils/api/jobs';
import Loader from '../../components/Loader';
import JobItem from './JobItem';
import Pagination from '../../components/Pagination';
import eventHub from '../../utils/eventhub';
import AddJob from './AddJob';
import { mapGetters } from 'vuex';

export default {
	name: 'Jobs',
	data() {
		return {
			jobs: [],
			jobsLoading: true,
			jobsLoaded: false,
			limit: 6,
			offset: 0,
			total: 0,
			loading: true,
			baseUrl: 'jobs'
		};
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;

		/**
		 * React and refresh list of jobs
		 */
		eventHub.$on('job-created', (job) => {
			this.jobs.unshift(job);
		});
		if (this.getLoggedUser.category === 'administrator') {
			this.getJobs();
		} else {
			this.getEmployerJobs();
		}
	},
	methods: {
		getJobs() {
			this.jobsLoading = true;
			getJobs(this.limit, this.offset)
				.then((response) => {
					if (response.status === 200) {
						this.jobs = response.data.data;
						this.total = response.data.total;
						this.jobsLoaded = true;
						return true;
					}
					this.jobsLoaded = false;
					return false;
				})
				.catch((error) => {
					this.jobsLoaded = false;
					return error;
				})
				.finally(() => {
					this.jobsLoading = false;
				});
		},
		getEmployerJobs() {
			getEmployerJobs(this.getLoggedUser.id)
				.then((response) => {
					if (response.status === 200) {
						this.jobs = response.data.data;
						this.total = response.data.total;
						this.jobsLoaded = true;
						return true;
					}
					this.jobsLoaded = false;
					return false;
				})
				.catch((error) => {
					this.jobsLoaded = false;
					return error;
				})
				.finally(() => {
					this.jobsLoading = false;
				});
		}
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getJobs();
		}
	},
	components: {
		JobItem,
		Loader,
		Pagination,
		AddJob
	},
	computed: {
		...mapGetters([
			'getLoggedUser'
		])
	}
};
</script>

<style lang="scss" scoped>
.contacts--list__item {
    display: grid;
    grid-template-columns: 35px 2fr 1fr 1fr 1fr 150px;
}
</style>
