<template>
	<div
	id="userModal"
	aria-labelledby="contactModalTitle"
	class="modal fade"
	tabindex="-1"
	role="dialog"
	aria-hidden="true">
	<div class="modal-dialog modal-lg" role="document">
		<div class="modal-content">
			<div class="modal-header modal__header">
				<h3 class="modal-title" id="contactModalTitle">Add Job</h3>
				<button
				@click.prevent="clearErrors()"
				type="button"
				class="close"
				data-dismiss="modal"
				aria-label="close">
				<span aria-hidden="true">&times;</span>
				</button>
			</div>

			<div class="modal-body modal__body">
				<form
				class="modal__form"
				method="post"
				@submit.prevent="createJob">
				<div class="form-row">
					<div class="form-group col-md-6">
					<label class="modal__label" for="fname">Title</label>
					<input
						:class="[{'is-invalid': errors.title}]"
						@keydown="clearErrors($event.target.name)"
						v-model="itemData.title"
						name="fname"
						value=""
						type="text"
						class="form-control"
						id="jobTitle"
						placeholder="Title">
					<div class="invalid-feedback">
						{{ errors.title }}
					</div>
					</div>
					<div class="form-group col-md-6">
					<label class="modal__label" for="lname">Type</label>
					<select
						@change="setJobType($event)"
						@keydown="clearErrors($event.target.name)"
						:class="[{'is-invalid': errors.type}]"
						class="custom-select form-control" name="">
						<option selected value="">Type</option>
						<option value="full-time">Full Time</option>
						<option value="contract">Contract</option>
					</select>
					<div class="invalid-feedback">
						{{ errors.type }}
					</div>
					</div>
				</div>
				<div class="form-row">
					<div class="form-group col-md-6">
						<label class="modal__label" for="phone">Location</label>
						<select 
							class="custom-select form-control"  
							id="career"
							:class="[{'is-invalid': errors.location}]"
							v-model="itemData.location">
							<option selected disabled>Select a location for the job</option>
							<option 
								v-for="(state, i) in states"
								:key="i"
								:value="state.value">{{state.text}}</option>
						</select>
						<div class="invalid-feedback">
							{{ errors.location }}
						</div>
					</div>
					<div class="form-group col-md-6">
						<label class="modal__label" for="career">Career Track</label>
						<select 
							class="custom-select form-control"  
							id="career"
							:class="[{'is-invalid': errors.career}]"
							v-model="itemData.career_id">
							<option selected disabled>Select a career for this job</option>
							<option 
								v-for="(career, i) in careers"
								:key="i"
								:value="career.id">{{career.name}}</option>
						</select>
						<div class="invalid-feedback">
							{{ errors.career_id }}
						</div>
					</div>
				</div>
				<div class="form-row">
					<div class="form-group col-md-12">
						<label class="modal__label" for="email">Closing Date</label>
						<date-picker v-model="itemData.closing_date" :config="options">
						</date-picker>
						<div class="invalid-feedback">
							{{ errors.closing_date }}
						</div>
					</div>
				</div>
				<div class="form-group">
					<label class="modal__label" for="address">Description</label>
					<quill-editor ref="myTextEditor"
						v-model="itemData.description"
						:options="editorOption">
					</quill-editor>
				</div>
				</form>
			</div>
			<div class="modal-footer modal__footer">
				<button
				@click.prevent="clearErrors()"
				class="btn btn--md"
				type="button"
				data-dismiss="modal"
				aria-label="close"
				name="button">Cancel
				</button>
				<button
					@click.prevent="createJob"
					class="btn btn--md btn__primary"
					type="button"
					v-bind="{'disabled': loading}"
					name="button">Add Job
				</button>
			</div>
		</div>
	</div>
	</div>
</template>

<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import datePicker from 'vue-bootstrap-datetimepicker';
import '@fortawesome/fontawesome-free/css/fontawesome.css';
import '@fortawesome/fontawesome-free/css/regular.css';
import '@fortawesome/fontawesome-free/css/solid.css';
import 'pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css';
import { createJob } from '../../utils/api/jobs';
import job from '../../utils/validations/job';
import eventHub from '../../utils/eventhub';
import form from '../../mixins/form';
import { success, fail } from '../../utils/toast';
import states from './locations';
import { getCareers } from '../../utils/api/career';

export default {
	name: 'AddJob',
	mixins: [form],
	data() {
		return {
			itemData: {
				title: '',
				type: '',
				location: '',
				closing_date: '',
				description: ''
			},
			errors: {},
			options: {
				format: 'YYYY-MM-DD',
				useCurrent: true
			},
			states,
			loadingCareers: true,
			careersLoaded: false,
			loading: false,
			careers: [],
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	methods: {
		setJobType(e) {
			this.itemData.type = e.target.value;
		},
		/**
		 * This creates a new job
		 */
		createJob() {
			/**
			 * Validate input
			 */
			this.itemData.user_id = this.$session.get('id');
			this.validate(this.itemData, job.create);
			if (Object.keys(this.issues).length > 0) {
				this.errors = this.issues;
				return false;
			}

			createJob(this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.clearFormField();
						/**
						 * Bubble event back up for reactivity
						 */
						eventHub.$emit('job-created', response.data);
						success({
							title: 'Success! New job has been added'
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		getCareers() {
			this.loadingCareers = true;
			getCareers()
				.then((response) => {
					if (response.status === 200) {
						this.careers = response.data.data;
						this.careersLoaded = true;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.loadingCareers = false;
					return error;
				});
			return true;
		}
	},
	components: {
		datePicker,
		quillEditor
	},
	created() {
		this.getCareers();
	},
	computed: {
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	}
};
</script>

<style scoped>
.quill-editor {
    height: 20rem;
}
.modal__footer {
	margin-top: 7em;
}
</style>
